<template>
    <div class="cart">
        <table cellspacing="0" cellpadding="0" width="640">
            <tr>
                <th>选择</th>
                <th>课程名称</th>
                <th>课程价格</th>
                <th>
                    课程数量
                </th>
                <th>价格</th>
            </tr>
            <tr v-for="(item,index) in carts" :key="index">
                <td><input @change="changeSelect(item)" type="checkbox" v-model="item.isActive"></td>
                <td>{{item.name}}</td>
                <td>{{item.price}}</td>
                <td>
                    <button @click="mNumber(item)"> - </button>
                    {{item.number}}
                    <button  @click="addNumber(item)"> + </button>
                </td>
                <td>{{item.number*item.price}}</td>
            </tr>
        </table>


        <span class="all">
            <span><b>选中：{{selectCart}}</b></span>
            <span><b>总价：{{allPrice}}</b></span>
        </span>


    </div>
</template>

<script>
export default {
    /**数据 */
    data() {
        return {
            
        }
    },
    /**父子数据传递属性 */
    props:["carts"],
    /**方法 */
    methods: {
        changeSelect(item){
            this.$emit("changelist",item);
        },
        addNumber(item){
            item.number=++item.number;
            this.$emit("changelist",item);
            
        },
        mNumber(item){
            item.number=--item.number;
            if(item.number>0){
                this.$emit("changelist",item);
            }else{
                if(window.confirm("确定删除购物车里的课程吗？")){
                   this.$emit("cartDeleteCourse",item); 
                }
            }
        }
    },
    computed:{
        allPrice(){
            return this.carts.reduce((start,item)=>{
                return start+item.price*item.number;
            },0);
        },
        selectCart(){
            let selected=this.carts.filter(item=>{
                return item.isActive==true;
            });
            return selected.length+""+"/"+this.carts.length;
        }
    }
}
</script>

<style scoped>
    .all{
        display: block;
        margin-top:10px;
    }
    .all span{
        display: block;
    }
    .all span:nth-of-type(1){
        text-align: left;
    }
    .all span:nth-of-type(2){
        text-align: right;
    }
</style>