<template>
  <div id="app">
    <h2>vue实现一个简单的购物车</h2>
    <h3>课程添加</h3>
    <div class="course-add">
      <label > 课程名称:</label>
      <input  type="text" v-model="add_to_cart.name"/>
      <br>
      <label>课程价格:</label>
      <input type="text" v-model="add_to_cart.price"/>
      <br>
      <button @click="add_to_cartlist" >添加课程</button>
    </div>


    <h3>课程显示列表</h3>
    <div class="course-list">
      <table width="640" cellpadding="0" cellspacing="0">
        <tr>
          <th>课程id号</th>
          <th>课程名称</th>
          <th>课程价格</th>
          <th>操作</th>
        </tr>
        <tr v-for="(item,index) in cartlist" :key="index">
          <td>{{item.id}}</td>
          <td>{{item.name}}</td>
          <td>¥{{item.price}}</td>
          <td><button @click="addcart(item)">添加到购物车</button></td>
        </tr>
      </table>
    </div>
    <h3>购物车</h3>
    <div class="cart">
      <cart :carts="carts"
       @changelist="appChangelist" 
       @cartDeleteCourse="deleteCourse"
      ></cart>
    </div>
  </div>
</template>
<script>
// 引进购物车
import Cart from "./components/Cart.vue";


export default {
  data() {
    return {
      cartlist:[
        {
          id:1,
          name:"javascript",
          price:10000
        }
      ],
      add_to_cart:{
        name:"",
        price:""
      },
      carts:[
        {
          id:1,
          name:"javascript",
          price:10000,
          isActive:true,
          number:1
        }
      ]

    }
  },
  /**方法 */
  methods: {
    add_to_cartlist(){
      if(!this.add_to_cart.name || !this.add_to_cart.price){
        alert("数据不能够为空！请重新操作");
        return;
      }

      
      //课程名不能够重复
      let isName=this.cartlist.some(item=>{
        return item.name==this.add_to_cart.name;
      });
      if(isName){
        return window.confirm("课程名称不能够重名");
      }

      //判断数据列表里边是否是有数据的
      if(this.cartlist.length===0){
        this.add_to_cart.id=1;
      }else if(this.cartlist.length===1){
        this.add_to_cart.id=this.cartlist[0].id+1;
      }else{
        let listmax=0;
        //数据的id是最大的id后边加1
       listmax=this.cartlist.reduce((prev,item)=>{
          if(item.id>prev){
            prev=item.id;
          }
          return prev;
        },0);
        this.add_to_cart.id=listmax+1;
      }
      
      this.cartlist.push({
        ...this.add_to_cart
      });

      console.log(this.cartlist);
    },

    //添加到购物车的方法
    addcart(item){
      let isAdd=false;
      //往购物车的列表里边添加数据
      this.carts.forEach(cart=>{
        if(item.id==cart.id){
          isAdd=true;
          cart.number++;
          return;
        }
      });
      if(!isAdd){
        this.carts.push({
          ...item,
          number:1
        });
      }
    },
    /**子元素改变父元素的方法 */
    appChangelist(cart){
      this.carts.forEach(item=>{
        if(item.id==cart.id){
          item=cart;
        }
      });
    },
    deleteCourse(item){
      this.carts=this.carts.filter(cart=>{
        return cart.id!=item.id; 
      });
      console.log(this.carts);
    }
  },
  
  // 注册插件
  components:{
    cart:Cart
  }

}
</script>>
